import rawCsvData from "./data"
import styles from "./style.module.scss";
import image1 from './carnation_hanataba.png';
import imageOn from './audio-on.svg';
import imageOff from './audio-off.svg';
import { useCallback, useEffect, useRef, useState } from "react";
import * as Papa from 'papaparse';

const bgm = require("./bgm2.mp3");

type MessageData = {
  name: string,
  message: string,
  x: number,  
}

function buildMessageData() : MessageData[] {
  const result: MessageData[] = [];
  const parsed = Papa.parse(rawCsvData, {
    header: true
  });
  parsed.data.forEach((element) => {
    const data = element as {
      name: string,
      message: string,
    }
    result.push({
      name : data.name,
      message: data.message,
      x:  Math.random(),
    })    
  });
  
  let currentIndex = result.length;
  let tempValue: MessageData;
  let randomIndex: number;

  while (currentIndex !== 0) {
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    tempValue = result[currentIndex];
    result[currentIndex] = result[randomIndex];
    result[randomIndex] = tempValue;
  }
  return result;
}


const ThankPage2023 = () => {
  const [messages, setMessages] = useState<MessageData[]>(buildMessageData());
  const animationRef = useRef<number | null>(null);
  
  const scrollRef = useRef<HTMLDivElement>(null);
  const lastTimeRef = useRef<DOMHighResTimeStamp>(0);  

  const [isPlaying, setIsPlaying] = useState(false);
  const audioRef = useRef<HTMLAudioElement>(null);

  const animate = useCallback((time : DOMHighResTimeStamp) => {
    if(scrollRef.current) 
    {
      const { scrollTop, scrollHeight, clientHeight } = scrollRef.current;
      if (scrollTop + clientHeight >= scrollHeight - 1) {
        setMessages(buildMessageData());
        scrollRef.current.scrollTop = 0;
        lastTimeRef.current = time;
      } else {
        if (lastTimeRef.current != 0) {
          const speed:number = 30.0 / 1000.0;
          const lastScrollTop = scrollRef.current.scrollTop;
          scrollRef.current.scrollTop += (time - lastTimeRef.current) * speed;
          lastTimeRef.current += (scrollRef.current.scrollTop - lastScrollTop) / speed;
        } else {
          lastTimeRef.current = time;
        }
      }
    }
    animationRef.current = requestAnimationFrame(animate);
  }, []);

  useEffect(() => {
    animationRef.current = requestAnimationFrame(animate);

  return () => {
      if (animationRef.current)
      {
        cancelAnimationFrame(animationRef.current);
      }   

    }
  }, [animate]);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = 0.2;
    }

  }, [audioRef.current]);

  const handleTogglePlay = () => {
    if (audioRef.current)
    {
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        audioRef.current.play();
      }
      setIsPlaying(!isPlaying);
    }
  };
  return (
    <div className={styles.bg}>
      <img src={image1} className={styles.bgImage} alt="bgImage" />   
      <div className={styles.header}>
        보평초등학교 선생님<br/>
        감사합니다
      </div>

      <div className={styles.scrollBox} ref={scrollRef}>       
        <div className={styles.blank}></div>
        {
          messages.map((data, index) => {
            return (
              <div className={styles.balloon} style = {{transform: ` translateX(calc(${data.x} * (100vw - min(50vw, 400px) - 20px))`}} key={index}>
                <div className={styles.message}>{data.message}</div>
                <div className={styles.name}>{data.name}</div>
              </div>
            );
          })
        }
      </div>

      <audio ref={audioRef} src={bgm} autoPlay loop/>
      <img src={isPlaying ? imageOn : imageOff} className={styles.audioButton} alt="toggle" onClick={handleTogglePlay}/>  
    </div>
  );
}

export default ThankPage2023;